                                       <div class="form-group">

                                          <label>Frequency - KM Run threshold</label>
                                          <input v-model="form.run_km_threshold" type="text" name="run_km_threshold"
                                             class="form-control"
                                             :class="{ 'is-invalid': verrors.has('run_km_threshold') }"
                                             v-validate="'required'" data-vv-as="Run KM threshold" placeholder="">
                                          <div v-if="verrors.has('run_km_threshold')"
                                             class="help-block invalid-feedback">
                                             {{ verrors.first('run_km_threshold') }}
                                          </div>
                                       </div>
                                    </div>
                                 </div>
                              </div>
                           </div>
                           <div class="card col-12">
                              <div class="card-header">
                                 <div class="form-check form-check-inline">
                                    <input class="form-check-input" type="checkbox" id="cc8" value="1"
                                       v-model="form.is_production_fig">
                                    <label class="form-check-label" for="cc8">Production Maintenance ?</label>
                                 </div>
                              </div>
                              <div class="card-body" v-if="form.is_production_fig">
                                 <div class="row">
                                    <div class="col-6">
                                       <div class="form-group">
                                          <label>Last Production fig of Periodic Maintenance /Breakdown
                                             Maintenance</label>
                                          <input type="text" placeholder="i.e. 1000 Cubic Meters (Cu Mtr)"
                                             v-model="form.periodic_maintenance_breakdown"
                                             name="periodic_maintenance_breakdown" class="form-control"
                                             :class="{ 'is-invalid': verrors.has('periodic_maintenance_breakdown') }"
                                             v-validate="'required'" data-vv-as="Machine Type" />
                                          <div v-if="verrors.has('periodic_maintenance_breakdown')"
                                             class="help-block invalid-feedback">
                                             {{ verrors.first('periodic_maintenance_breakdown') }}
                                          </div>
                                       </div>
                                    </div>
                                    <div class="col-3">
                                       <div class="form-group">
                                          <label>Present Production fig</label>
                                          <input v-model="form.present_production_fig" type="text"
                                             name="present_production_fig" class="form-control"
                                             :class="{ 'is-invalid': verrors.has('present_production_fig') }"
                                             v-validate="'required'" data-vv-as="Present Production fig" />
                                          <div v-if="verrors.has('present_production_fig')"
                                             class="help-block invalid-feedback">
                                             {{ verrors.first('present_production_fig') }}
                                          </div>
                                       </div>
                                    </div>
                                    <div class="col-3">
                                       <div class="form-group">
                                          <label>Frequency - Production fig threshold</label>
                                          <input v-model="form.production_fig_threshold" type="text"
                                             name="production_fig_threshold" class="form-control"
                                             :class="{ 'is-invalid': verrors.has('production_fig_threshold') }"
                                             v-validate="'required'" data-vv-as="Production fig threshold" />
                                          <div v-if="verrors.has('production_fig_threshold')"
                                             class="help-block invalid-feedback">
                                             {{ verrors.first('production_fig_threshold') }}
                                          </div>
                                       </div>
                                    </div>
                                 </div>
                              </div>
                           </div>
                        </template>


                        <div class="col-12">
                           <div class="form-group">
                              <label>Allocate to Site</label>
                              <v-select label="site_name" :reduce="(option) => option.id" :options="sites"
                                 placeholder="Choose Site ..." v-model="form.site_id" v-validate="'required'"
                                 :class="{ 'is-invalid': verrors.has('site_id') }" data-vv-name="site"
                                 class="required">
                              </v-select>
                              <div v-if="verrors.has('site_id')" class="help-block invalid-feedback">
                                 {{ verrors.first('site_id') }}
                              </div>
                           </div>
                        </div>


<!--                         <div class="col-12">
                           <div class="form-group">
                              <label>Next PM DATE</label>
                              <input v-model="form.present_production_fig" type="text" name="next_pm_date"
                                 class="form-control" :class="{ 'is-invalid': verrors.has('next_pm_date') }"
                                 v-validate="'required'" data-vv-as="Next PM DATE" disabled />
                              <div v-if="verrors.has('next_pm_date')" class="help-block invalid-feedback">
                                 {{ verrors.first('next_pm_date') }}
                              </div>
                           </div>
                        </div> -->

                        <div class="row">
                            <div class="col text-center">
                                <button class="btn btn-success" type="submit">Submit</button>
                            </div>
                        </div>

                     </form>
                  </div><!-- /.card-body -->
               </div>
               <!-- /.nav-tabs-custom -->
            </div>
            <!-- end tabs -->
         </div>
      </div>
   </section>
</template>

<script>
import VueTagsInput from '@johmun/vue-tags-input';
import { now } from 'moment';
import {
   mapGetters
} from "vuex";
export default {
   name: "UserAddEdit",
   components: {
      VueTagsInput,
   },
   filters: {
      truncate: function (text, length, suffix) {
         return text.substring(0, length) + suffix;
      },
   },
   computed: {
      filteredItems() {
         return this.autocompleteItems.filter(i => {
            return i.text.toLowerCase().indexOf(this.tag.toLowerCase()) !== -1;
         });
      },
   },
   data() {
      return {
         checkbox_model: [],
         form: new Form({
            id:'',far_no:'',name:'',category_id:'',sub_category_id:'',machine_companies_id:'',machine_models_id:'',owner:'Owned',purchase_date:'',machine_type:'New',manufacture_year:'',regn_no:'',machine_sr_no:'',engine_model:'',engine_no:'',chassis_no:'',condition_of_machine:'',standard_cph:'',consumption_type:'Fuel',under_maintenance:'Yes',is_maintenance_date:'',is_consumtion_hr_run:'',is_consumtion_km_run:'',is_production_fig:'',
            date_time_machine_ic: new Date().toISOString(),
            date_threshold:'',run_hour_threshold:'',run_km_threshold:'',production_fig_threshold:'', site_id:''

         }),
         categories: [],
         sub_categories: [],
         companies: [],
         models: [],

         sites: [],
         users: [],
         tag: '',
         autocompleteItems: [],

         sl: 0,
         assigned_ids: [],
         permissions: [],
         view_mode: false,
         countries: [],
         states: [],
         cities: [],
         editmode: false,
         step: 1,
         users: {},

      }
   },
   computed: {
      ...mapGetters(["errors"]),
   },
   beforeCreate() {
      axios.get("api/get_pre_machine")
         .then((res) => {
            this.categories = res.data.data.Category;
            this.companies = res.data.data.MachineCompany;
            this.sites=res.data.data.sites;
         })
   },
   methods: {

      submit_machine() {
         this.$validator.validateAll().then(async (valid) => {
            if (valid) {
               this.verrors.clear();
               let cloaderd = this.$loading.show({
                  container: this.$refs.ref_load_user
               });

               if (this.form.id > 0) {
                  this.form.put('api/machine/' + this.form.id)
                     .then((response) => {
                        this.$validator.reset();
                        cloaderd.hide()
                        if (response.data.success == true) {

                           this.form.reset();
                           Toast.fire({
                              icon: 'success',
                              title: response.data.message
                           });
                           this.$router.push('/machines').catch(() => { });
                        }
                     })
                     .catch(err => {
                        cloaderd.hide();
                        if (err.response && err.response.data) {
                           this.$setErrorsFromResponse(err.response.data);
                        }
                     })
               } else {
                  this.form.post('api/machine')
                     .then((data) => {
                        this.$validator.reset();
                        cloaderd.hide()
                        this.form.id = data.data.data.id;
                        this.form.reset();
                        Toast.fire({
                           icon: 'success',
                           title: data.data.message
                        });
                     })
                     .catch(err => {
                        if (err.response && err.response.data) {
                           this.$setErrorsFromResponse(err.response.data);
                        }
                        cloaderd.hide();
                     })
               }
            }
         });
      },

      grand_permission() {
         let cloaderd = this.$loading.show({
            container: this.$refs.setting_permissionref
         });

         axios.post('api/user/grand_permission', {
            user: this.form.id,
            permissions: this.assigned_ids
         })
            .then((response) => {
               cloaderd.hide()
               if (response.data.success == true) {
                  Toast.fire({
                     icon: 'success',
                     title: response.data.message
                  });
               }
            })
            .catch(() => {
               cloaderd.hide();
            })
      },

   },
   mounted() {
      if (this.$route.params.id) {
         this.form.id = this.$route.params.id;
         let cloaderd = this.$loading.show({
            container: this.$refs.ref_load_user
         });
         axios.get("api/machine/" + this.$route.params.id)
            .then((res) => {
               this.form.fill(res.data.data)
               let date_time_machine_ic = res.data.data.date_time_machine_ic
               this.form.date_time_machine_ic = new Date(date_time_machine_ic).toISOString()
               cloaderd.hide();
            })
            .catch(() => {
               cloaderd.hide();
               this.$router.push('/machines/').catch(() => { });
            })

      }
   },
   created() { },
   watch: {

      async 'form.category_id'(n,o) {
         if(o)
         this.form.sub_category_id = ''
         if (n) {
            axios.get("api/category/" + n)
               .then((res) => {
                  this.sub_categories = res.data.data;
               })
         }
      },
      async 'form.machine_companies_id'(n,o) {
          if(o)
         this.form.machine_models_id = ''
         if (n) {
            axios.get("api/model/" + n)
               .then((res) => {
                  this.models = res.data.data;
               })
         }
      },
   }
}
</script>
